html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::after,
*::before {
  box-sizing: inherit;
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fix-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.header {
  background-color: #4CB8B3;
}
.header h1 {
  text-align: center;
  color: #fafafafa;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .header-content {
  padding: 1rem;
}
@media (min-width: 768px) {
  .header .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
  }
}

@media (min-width: 768px) {
  .main-nav {
    display: flex;
    gap: 3rem;
  }
}
.main-nav a {
  font-size: 2.4rem;
  color: #fafafafa;
  text-align: center;
  display: block;
  margin-bottom: 1rem;
}
.main-nav a:hover {
  color: #F3F46D;
}
@media (min-width: 768px) {
  .main-nav a {
    margin: 0;
    font-size: 2rem;
  }
}
@media (min-width: 992px) {
  .main-nav a {
    font-size: 2.4rem;
  }
}

.video {
  position: relative;
  max-height: 60rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: #f2226c;
  background: linear-gradient(90deg, rgba(242, 34, 108, 0.2) 0%, rgba(132, 0, 255, 0.5) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.video-content {
  z-index: 1;
}
.video-content h2 {
  color: #fafafafa;
  font-size: 7rem;
  margin: 0;
}
.video-content p {
  display: inline-block;
  background-color: #F53756;
  padding: 1rem 2rem;
  color: #fafafafa;
  font-weight: 700;
}

.festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.festival-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .festival-content {
    margin-top: 0;
  }
}
.festival-content h2 {
  margin-bottom: 2rem;
}
.festival-content .date {
  font-weight: 700;
  color: #F53756;
}

.lineup {
  background-color: #F53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #fafafafa;
}
.lineup .day {
  margin: 0;
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .events-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.event {
  padding-top: 2rem;
  margin-top: 3rem;
}
.event p {
  color: #000000;
}
.event p::after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0 auto;
}

.rock .event__name::after {
  background-image: url("../img/icono_rock.png");
}

.edm .event__name::after {
  background-image: url("../img/icono_edm.png");
}

.calendar {
  list-style: none;
  padding: 0;
  margin: 0;
}
.calendar li {
  padding: 2rem;
  font-weight: 700;
}
.calendar li span {
  font-weight: 400;
}

.bg-green {
  background-color: #4CB8B3;
}
.bg-green li:nth-child(odd) {
  background-color: #3b9691;
}

.bg-yellow {
  background-color: #F3F46D;
}
.bg-yellow li:nth-child(odd) {
  background-color: #eff03e;
}

.galery {
  padding: 5rem 0;
}
.galery h3 {
  text-align: center;
}

.images-galery {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  justify-content: center;
}
.images-galery picture {
  margin: 0 auto;
}
@media (min-width: 480px) {
  .images-galery {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .images-galery {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .images-galery {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: grid;
  place-items: center;
  z-index: 2;
}
.overlay picture {
  display: block;
  max-width: 80rem;
  margin: 0 20px;
  border-radius: 15px;
}
@media (min-width: 992px) {
  .overlay picture {
    margin: 0;
  }
}

.btn-close {
  height: 4rem;
  width: 4rem;
  border: none;
  cursor: pointer;
  border-radius: 50%;
  background-color: #fafafafa;
  font-weight: 700;
  display: grid;
  place-items: center;
  cursor: pointer;
  outline: none;
  transition: all 0.4s ease;
}
.btn-close:focus, .btn-close:hover {
  transform: scale(1.2);
  background-color: #F3F46D;
}

.tickets {
  padding: 5rem 0;
}
.tickets h3 {
  text-align: center;
  color: #fafafafa;
}

@media (min-width: 768px) {
  .tickets-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase .day {
  text-align: center;
  color: #fafafafa;
}
.pase .pase-header, .pase .pase-footer {
  padding: 0.2rem;
}
.pase .pase-header {
  background-color: #F53756;
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase .pase-header p {
  text-align: center;
  color: #fafafafa;
}
.pase .pase-body {
  background-color: #fafafafa;
}
.pase .pase-body ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.pase .pase-body ul li, .pase .pase-body ul :nth-child(odd) {
  padding: 1rem;
  background-color: #fafafafa;
  border-bottom: 1px solid #e1e1e1;
}
.pase .pase-footer {
  background-color: #752F97;
}
.pase .pase-footer p {
  text-align: center;
  color: #fafafafa;
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  background-color: #F53756;
  padding: 1rem;
}
.site-footer p {
  color: #fafafafa;
  text-align: center;
  margin: 0;
}