@use 'utils/variables' as v;
@use 'utils/mixins' as m;

.tickets{
    padding:5rem 0;
    h3{
        text-align: center;
        color: v.$blanco;
   }
}
.tickets-container{
    @include m.tablet {
        @include m.gridColumn(2, 5rem);
    }
}
.pase{
    .day{
        text-align: center;
        color: v.$blanco;
    }
    .pase-header, .pase-footer{
        padding: 0.2rem;
    }
    .pase-header{
        background-color: v.$rosa;
        text-transform: uppercase;
        font-size: 1.6rem;
        p{
            text-align: center;
            color: v.$blanco;
        }
    }
    .pase-body{
        background-color: v.$blanco;

        ul{
            @include m.resetList;
            li, :nth-child(odd){
                padding: 1rem;
                background-color: v.$blanco;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }
    .pase-footer{
        background-color: v.$morado;
        p{
            text-align: center;
            color: v.$blanco;
            font-size: 2.4rem;
            font-weight: 700;
        }
    }
}
