@use 'utils/mixins' as m;
@use 'utils/variables' as v;

.video{
    position: relative;
    max-height: 60rem;
    min-height: 50rem;
    overflow: hidden;
    .overlay{
        position: absolute;
        background: rgb(242,34,108);
        background: linear-gradient(90deg, rgba(242,34,108,0.2) 0%, rgba(132,0,255,0.5) 100%);  
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    video{
        width: auto;
        display: block;
        @include m.tablet {
            width: 100%;
        }
    }
}
.video-content{
    z-index: 1;
    h2{
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;
    }
    p{
        display: inline-block;
        background-color: v.$rosa;
        padding: 1rem 2rem;
        color: v.$blanco;
        font-weight: 700;
    }
}
