@use 'utils/variables'as v;
@use 'utils/mixins'as m;

.header {
    background-color: v.$verde;

    h1 {
        text-align: center;
        color: v.$blanco;

        @include m.tablet {
            margin: 0;
        }
    }

    .header-content {
        padding: 1rem;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 2rem;
        }
    }
}

.main-nav {
    @include m.tablet {
        display: flex;
        gap: 3rem;

    }
    a {
        font-size: 2.4rem;
        color: v.$blanco;
        text-align: center;
        display: block;
        margin-bottom: 1rem;

        &:hover {
            color: v.$amarillo;
        }

        @include m.tablet {
            margin: 0;
            font-size: 2rem;
        }

        @include m.desktop {
            font-size: 2.4rem;
        }
    }
}