@use 'utils/variables' as v;
@use 'utils/mixins' as m;


.lineup{
    background-color: v.$rosa;
    padding: 5rem 0;
    h3, p{
        text-align: center;
        color: v.$blanco;
    }
    .day{
        margin: 0;
        text-transform: uppercase;
        margin: 5rem 0;
    }
}
.events-container{
    @include m.tablet {
        @include m.gridColumn(2, 5rem);
    }
}
.event{
    padding-top: 2rem;
    margin-top: 3rem;
   p{
       color: v.$negro;
       &::after{
        content: '';
        display: block;
        width: 20rem;
        height: 10rem;
        background-size: 15rem;
        background-repeat: no-repeat;
        background-position: center center;
        margin: 0 auto;
 
    }
   }

}
.rock .event__name::after{
    background-image: url("../img/icono_rock.png")
}
 .edm .event__name::after{
    background-image: url("../img/icono_edm.png")
}
.calendar{
    @include m.resetList;
    li{
        padding: 2rem;
        font-weight: 700;
        span{
            font-weight: 400;
        }
    }
}
.bg-green{
    background-color: v.$verde;

    li:nth-child(odd){
        background-color: darken(v.$verde, 10%);
    }
}
.bg-yellow{
    background-color: v.$amarillo ;
    li:nth-child(odd){
        background-color: darken(v.$amarillo, 10%);
    }
}