@use '../utils/variables' as v;
html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *::after,
*::before{
    box-sizing: inherit;
    scroll-behavior: smooth;
}
body{
    font-family: v.$fuentePrincipal;
    font-size: 1.6rem;
    &.fix-body{
        overflow: hidden;
    }
}

p{
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.5;
}
a{text-decoration: none;}

h1,h2,h3{
    margin: 0 0 5rem 0;
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}
img{
    max-width: 100%;
    display: block;
}

.container{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}