@use 'variables' as v;
@mixin phone {
    @media (min-width: v.$phone) {
        @content
    }
};
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content
    }
};
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content
    }
};
@mixin desktopXL {
    @media (min-width: v.$desktopXL) {
        @content
    }
};

@mixin gridColumn($column, $spacing){
    display: grid;
    grid-template-columns: repeat($column, 1fr);
    gap: $spacing;
};

@mixin resetList{
    list-style: none;
    padding: 0;
    margin: 0;
};

@mixin btnReset($height,$widht){
    height: $height;
    width: $widht;
    border: none;
    cursor: pointer;
};