@use 'utils/variables' as v;
@use 'utils/mixins' as m;

.festival{
    padding: 5rem 0;
    @include m.tablet {
        @include m.gridColumn(2, 5rem)
    }
}
.festival-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
    text-align: center;
    @include m.tablet {
        margin-top: 0;
    }
    h2{
        margin-bottom: 2rem;
    }
    .date{
        font-weight: 700;
        color: v.$rosa;
    }
}