@use 'utils/variables' as v;
@use 'utils/mixins' as m;

.galery{
    padding: 5rem 0;
    h3{
        text-align: center;
    }
}
.images-galery{
    picture{
        margin: 0 auto;
    }
    @include m.resetList;
    @include m.gridColumn(2, 2rem);
    @include m.phone{
        @include m.gridColumn(2, 2rem)
    }
    @include m.tablet {
        @include m.gridColumn(3, 2rem);
    }
    @include m.desktop {
        @include m.gridColumn(6, 2rem);
        
    }
    justify-content: center;
}
.overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: grid;
    place-items: center;
    z-index: 2      ;

    picture{
        display: block;
        max-width: 80rem;
        margin: 0 20px ;
        border-radius: 15px;
        @include m.desktop {
            margin: 0;
        }
    }
}
.btn-close{
    @include m.btnReset(4rem, 4rem);
    border-radius: 50%;
    background-color: v.$blanco;
    font-weight: 700;
    display: grid;
    place-items: center;
    cursor: pointer;                
    outline: none;
    transition: all 0.4s ease;
    &:focus, &:hover{
        transform: scale(1.2);
        background-color: v.$amarillo;
    }
}